* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.cards {
  display: flex;
  margin-bottom: 40px;
  align-items: center;
}
.flag {
  width: 200px;
  height: 100px;
  display: block;
}
.country-list div {
  font-size: 20px;
  margin-left: 10px;
}
.country-info {
  display: flex;
}
.country {
  display: flex;
}
.card {
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 300px;
}
